.App {
  text-align: center;
}
:root{
  --bs-warning: #ffc107;
  --bs-warning-shadow: #ffc10770;

}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.form-control:focus {
  border-color: var(--bs-warning);
  box-shadow: 0 0 0 0.2rem var(--bs-warning-shadow) ;
}
.nav-shadow{
  box-shadow: 0px 0px 15px 10px var(--bs-warning-shadow) ;
}
.marginb{
  margin-bottom: 80px;
}